/* Center the content in the middle of the page */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f7f7f7;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  margin: 0;
}

.home-container h1 {
  font-size: 3rem;
  color: #2c3e50;
  margin-bottom: 1rem;
  font-weight: bold;
}

.home-container p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  text-align: center;
  max-width: 600px;
}

.home-container a {
  text-decoration: none;
  color: #3498db;
}

.home-container a:hover {
  text-decoration: underline;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-links a {
  font-size: 1.2rem;
  color: #3498db;
}

.social-links a:hover {
  text-decoration: underline;
}
